@font-face {
    font-family: 'Product Sans';
    font-style: normal;
    font-weight: 400;
    src: 'src/assets/fonts/ProductSans-Medium.ttf';
  }

  $color-accent-1: #0012D7;

  //Variables
  :root {
    --color-bg: #212121;
    --color-bg-2: #424242;
    --color-text-1: #fff;
    --color-text-2: #bbb;
  }

  @media (prefers-color-scheme: light) {
    :root {
      --color-bg: #fff;
      --color-bg-2: #fff;
      --color-text-1: #212121;
      --color-text-2: #757575;
    }
  }

    $color-bg: var(--color-bg);
    $color-bg-2: var(--color-bg-2);
    $color-text-1: var(--color-text-1);
    $color-text-2: var(--color-text-2);

  $font: "Product Sans", sans-serif;
  $font-size: 24px;
  
  $rounding: 10px;
  
@mixin flex-center() {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin flex-left() {
    @include flex-center();
    justify-content: flex-start;
}

@mixin input-field($rounding, $color) {
    align-items: center;

    background: $color;

    padding: 15px 13px;
    margin-bottom: 3px;

    outline: none;
    border-radius: $rounding;
}

@mixin input-field-focus-border($rounding, $color) {
    position: absolute;

    background: $color;

    border-radius: 0 0 $rounding $rounding;

    bottom: 0;
    left: 50%;
    width: 0%;
    height: 0%;
    z-index: -1;
    transition: height 0.3s, width 0.3s 0.1s;
    transform: translate(-50%, 0);
}

